import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ErrorComponent } from './core/components/error/error.component';

const routes: Routes = [
  { path: 'student', loadChildren: () => import('./student/student.module').then(m => m.StudentModule) },
  { path: 'teacher', loadChildren: () => import('./teacher/teacher.module').then(m => m.TeacherModule) },
  { path: 'xyzabc', loadChildren: () => import('./xyzabc/xyzabc.module').then(m => m.XyzabcModule) },
  { path: 'tp', loadChildren: () => import('./tp/tp.module').then(m => m.TpModule) },
  { path: 'login', loadChildren: () => import('./login/login.module').then(m => m.LoginModule) },
  { path: 'error',  component: ErrorComponent },
  { path: '',   redirectTo: '/student', pathMatch: 'full' }, // redirect to `first-component`

  // { path: '', loadChildren: () => import('./login-zone/login-zone.module').then(m => m.LoginZoneModule) },
  // { path: 'register', loadChildren: () => import('./register/register.module').then(m => m.RegisterModule) },
  // { path: 'reservation', loadChildren: () => import('./reservation/reservation.module').then(m => m.ReservationModule) },
  { path: '**', component: ErrorComponent },
];
@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
