import { Component, OnInit } from '@angular/core';
import { ChartDataSets, ChartOptions } from 'chart.js';
import { Color, Label } from 'ng2-charts';

@Component({
  selector: 'app-reports-practice',
  templateUrl: './reports-practice.component.html',
  styleUrls: ['./reports-practice.component.css']
})
export class ReportsPracticeComponent implements OnInit {
  public lineChartData: ChartDataSets[] = [
    { data: [1200, 900, 1000, 1100, 1050], label: 'Total', lineTension: .3 },
    { data: [450, 500, 300, 500, 450], label: 'Math', lineTension: .3 },
    { data: [750, 400, 700, 600, 600], label: 'Reading and writing', lineTension: .3 }
  ];
  public lineChartLabels: Label[] = ['Diagnostic Test', 'Test 1', 'Test 2', 'Test 3', 'Test 4'];
  public lineChartOptions: ChartOptions = {

    responsive: true,
    scales: {
      yAxes: [{
        ticks: {
          beginAtZero: true, // 是否 從 0 開始分層
          min: 0,
          max: 1600
        }
      }]
    },
  };
  public lineChartColors: Color[] = [
    {
      borderColor: 'black',
      backgroundColor: 'rgba(255,0,0,0)',
    },
    {
      borderColor: 'blue',
      backgroundColor: 'rgba(255,0,0,0)',
    },
    {
      borderColor: 'green',
      backgroundColor: 'rgba(255,0,0,0)',
    },
  ];
  public lineChartLegend = true;
  public lineChartType = 'line';
  public lineChartPlugins = [];

  constructor() { }

  ngOnInit(): void {
  }

}
