import { CourseFeedbackEditComponent } from './../../share/dialog/course-feedback-edit/course-feedback-edit.component';
import { MatDialog } from '@angular/material/dialog';
import { Component, OnInit } from '@angular/core';
import { faMeh, faFrown, faTired, faSmile, faLaughWink, faCircle, faCheckCircle, faThumbsUp } from '@fortawesome/free-solid-svg-icons';
import { printConsole } from '../../core/services/common.service';

@Component({
  selector: 'app-assignment',
  templateUrl: './assignment.component.html',
  styleUrls: ['./assignment.component.css']
})
export class AssignmentComponent implements OnInit {
  faTired = faTired;
  faFrown = faFrown;
  faMeh = faMeh;
  faSmile = faSmile;
  faLaughWink = faLaughWink;
  faCircle = faCircle;
  faCheckCircle = faCheckCircle;
  faThumbsUp = faThumbsUp;

  startCount;
  hasFeedback = false;

  selectedWeek = 'week1';
  selectedSubject = 'Reading';

  feedbackPoint = 1;

  formatLabel(value: number) {
    if (value === 3) {
      return 2;
    }

    if (value === -3) {
      return -2;
    }

    return value;
  }

  constructor(private dialog: MatDialog) { }

  ngOnInit(): void {
  }

  feedbackStart(num: number) {
    this.startCount = num;
  }

  openCourseFeedback() {
    // closeOnNavigation 導航的時候是否關閉 dialog
    const dialogRef = this.dialog.open(CourseFeedbackEditComponent, {
      width: '65vw',
      data: '',
      closeOnNavigation: false,
      disableClose: true,
      maxHeight: '90vh'
    });

    dialogRef.afterClosed().subscribe(result => {
      printConsole('The CourseFeedbackEditComponent dialog set video date was closed', result);
    });
  }

}
