import { Component, OnInit, Input, OnChanges, SimpleChanges, Output, EventEmitter } from '@angular/core';
import { DateService } from '../../services/date.service';
import { MatSelectChange } from '@angular/material/select';

export interface Timer {
  hour: number;
  minute: number;
}

export interface LimitTime {
  minHour: number;
  maxHour: number;
  minMinute: number;
  maxMinute: number;
}

/**
 * @param disabledHour - 禁止選擇幾時前的數字
 * @param  disabledMinute - 禁止選擇幾分前的數字
 */
@Component({
  selector: 'app-timer',
  templateUrl: './timer.component.html',
  styleUrls: ['./timer.component.css']
})
export class TimerComponent implements OnInit, OnChanges {
  hourArray = [];
  minuteArray = [];
  hour: number = null;
  minute: number = null;

  constructor(private dateService: DateService) { }
  @Input() limitTime: LimitTime = null;
  @Output() timeEmitter = new EventEmitter<Timer>();

  ngOnInit(): void {
    if (this.limitTime) {
      this.disabledHourMinuteList(this.limitTime);
    } else {
      this.initHourMinuteList();
    }
  }

  ngOnChanges(changes: SimpleChanges) {
    const limitTime: LimitTime = changes.limitTime ? changes.limitTime.currentValue : null;
    if (limitTime) {
      this.disabledHourMinuteList(limitTime);
    } else {
      this.initHourMinuteList();
    }
  }

  selectHour(hour: MatSelectChange) {
    // console.log('TimerComponent selectHour', hour.value);
    this.hour = hour.value;
    this.emitTime();
  }

  selectMinute(minute: MatSelectChange) {
    // console.log('TimerComponent selectMinute', minute.value);
    this.minute = minute.value;
    this.emitTime();
  }

  reset() {
    this.hour = null;
    this.minute = null;
  }

  /**
   * 有時間才送出
   */
  private emitTime() {
    this.timeEmitter.emit({hour: this.hour, minute: this.minute});
  }

  /**
   *  初始化時和分陣列
   */
  private initHourMinuteList() {
    const hourArray = this.dateService.getHourList();
    hourArray.forEach(value => {
      this.hourArray.push({name: value, value, isDisabled: false});
    });
    const minuteArray = this.dateService.getMinuteList();
    minuteArray.forEach(value => {
      this.minuteArray.push({name: value, value, isDisabled: false});
    });
  }

  /**
   * 禁止選擇幾時和幾分前的數字
   * @param hourNumber [number] 0-23
   * @param minuteNumber [number] 0-59
   */
  private disabledHourMinuteList({minHour, maxHour, minMinute, maxMinute}) {
    this.hourArray = [];
    this.minuteArray = [];
    const hourArray = this.dateService.getHourList();
    hourArray.forEach((value, index) => {
      this.hourArray.push({name: value, value, isDisabled: minHour <= index && index <= maxHour});
    });
    const minuteArray = this.dateService.getMinuteList();
    minuteArray.forEach((value, index) => {
      this.minuteArray.push({name: value, value, isDisabled: minMinute <= index && index <= maxMinute});
    });
  }

}
