import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'taiwanFullDay'
})
export class TaiwanFullDayPipe implements PipeTransform {

  WEEKTEXT = ['日', '一', '二', '三', '四', '五', '六'];

  transform(time: Date, isWeekText): unknown {
    if (! time) { return null; }
    console.log('time', time);
    if (typeof time === 'string') {
      time = new Date(time);
    }
    if (isWeekText) {
      const taiwanYear = time.getFullYear() - 1911;
      const truthMonth = time.getMonth() + 1;
      const month = truthMonth < 10 ? '0' + truthMonth.toString() : truthMonth;
      const date = time.getDate() < 10 ? '0' + time.getDate().toString() : time.getDate();
      const weekText = this.WEEKTEXT[(time.getDay())];
      return `${taiwanYear}/${month}/${date} (${weekText})`;
    } else {
      const taiwanYear = time.getFullYear() - 1911;
      const truthMonth = time.getMonth() + 1;
      const month = truthMonth < 10 ? '0' + truthMonth.toString() : truthMonth;
      const date = time.getDate() < 10 ? '0' + time.getDate().toString() : time.getDate();
      return `${taiwanYear}/${month}/${date}`;
    }
  }

}
