import { MatDialog } from '@angular/material/dialog';
import { DateService } from './../../../core/services/date.service';
import { printConsole } from '../../../core/services/common.service';
import { HttpService } from './../../../core/services/http.service';
import { Component, OnInit } from '@angular/core';
import { faCircle, faExclamationTriangle, faStar } from '@fortawesome/free-solid-svg-icons';
import { DashboardLearningWeekType, getVideoStatus, LearningScheduleModel, LearningScheduleType, ResponseLearningScheduleType } from '../../../core/models/learning-schedule.model';
import { VideoPlayerComponent } from '../../dialog/video-player/video-player.component';
import { BreakpointObserver } from '@angular/cdk/layout';
import { of } from 'rxjs';

@Component({
  selector: 'app-learning-schedule',
  templateUrl: './learning-schedule.component.html',
  styleUrls: ['./learning-schedule.component.css']
})
export class LearningScheduleComponent implements OnInit {
  STATUS = {
    NOTYET: 'NOTYET',
    UNFINISH: 'UNFINISH',
    FINISH: 'FINISH'
  };

  faStar = faStar;
  faCircle = faCircle;
  faExclamationTriangle = faExclamationTriangle;
  isPortrait = false; // 是否是縱向畫面

  today = new Date();
  startDate = new Date('2021-02-21');
  endDate = new Date('2021-03-20');

  totalStar = 0;
  weekList: DashboardLearningWeekType[][] = null; // 取得 (每日影片清單 放到 單周) 在 放到 陣列
  currectWeekIndex = 0; // 目前要顯示 weekList 第幾個陣列
  currectDateIndex = 0; // 目前 week 第幾個星期 index (一二三四五)(01234)
  currectDate = null;

  constructor(private httpService: HttpService, private dateServive: DateService,
              private dialog: MatDialog, private breakpointObserver: BreakpointObserver

    ) { }

  ngOnInit(): void {
    this.breakpointObserver.observe('(orientation: portrait)').subscribe(result => {
      this.isPortrait = result.matches;
    });


    this.getMockList().subscribe( (res: any) => {
      const learningSchedule = new LearningScheduleModel(res.ResultData, this.dateServive.getDate(this.startDate),
      this.dateServive.getDate(this.endDate), this.dateServive.getDate(this.today));
      this.weekList = learningSchedule.getVideoOfDayInWeekWithoutWeekend();
      this.totalStar = learningSchedule.getTotalStar();
      console.log('singleWeekList', this.weekList);

      const isBefore = this.today < this.startDate;
      const isAfter = this.today > this.endDate;
      const isBetween = this.startDate < this.today && this.endDate > this.today;

      console.log('isBefore', isBefore);
      console.log('isAfter', isAfter);
      console.log('isBetween', isBetween);

      switch (true){
        case isBefore:
          this.currectWeekIndex = 0;
          this.currectDateIndex = 0;
          break;
        case isAfter:
          this.currectWeekIndex = this.weekList.length - 1;
          this.currectDateIndex = 0;
          break;
        case isBetween:
          this.currectWeekIndex = learningSchedule.getWeekIndexOfWeekList(this.today);
          this.currectDateIndex = this.today.getDay() - 1; // 一到五
          break;
      }
      printConsole('currectWeekIndex', this.currectWeekIndex);
      printConsole('currectDateIndex', this.currectDateIndex);
      this.weekList[this.currectWeekIndex][this.currectDateIndex].isSelected = true;
      this.currectDate = this.weekList[this.currectWeekIndex][this.currectDateIndex].date;

    });

  }

  last() {
    this.weekList[this.currectWeekIndex][this.currectDateIndex].isSelected = false;

    if (this.currectWeekIndex > 0) {
      this.currectWeekIndex -= 1;
    }
    this.currectDateIndex = 0;

    this.weekList[this.currectWeekIndex][this.currectDateIndex].isSelected = true;
    this.currectDate = this.weekList[this.currectWeekIndex][this.currectDateIndex].date;
  }

  next() {
    this.weekList[this.currectWeekIndex][this.currectDateIndex].isSelected = false;

    if (this.currectWeekIndex < (this.weekList.length - 1)) {
      this.currectWeekIndex += 1;
    }
    this.currectDateIndex = 0;

    this.weekList[this.currectWeekIndex][this.currectDateIndex].isSelected = true;
    this.currectDate = this.weekList[this.currectWeekIndex][this.currectDateIndex].date;
  }

  putDateIndex(dateIndex: number) {
    this.weekList[this.currectWeekIndex][this.currectDateIndex].isSelected = false;

    this.currectDateIndex = dateIndex;
    this.weekList[this.currectWeekIndex][this.currectDateIndex].isSelected = true;
    this.currectDate = this.weekList[this.currectWeekIndex][this.currectDateIndex].date;
  }

  playVideo(url: string) {
    // closeOnNavigation 導航的時候是否關閉 dialog
    const dialogRef = this.dialog.open(VideoPlayerComponent, {
      width: this.isPortrait ? '100vw' : '50vw',
      data: {
        url
      },
      closeOnNavigation: false,
      disableClose: true,
      maxHeight: '90vh',
      maxWidth: '100%'
    });

    dialogRef.afterClosed().subscribe(result => {
      printConsole('The dialog video player was closed', result);
    });
  }

  getVideoStatus(targetDate: Date, currectDate: Date, video: LearningScheduleType) {
    return getVideoStatus(targetDate, currectDate, video);
  }

  // 回傳出 數字的陣列
  emptyArray(num: number) {
    return Array.from(Array(num).keys());
  }

  // 無條件捨去
  mathFloor(num: number) {
    return Math.floor(num);
  }

  getMockList() {
    return of({
      ApiStatus: true,
      ResultData: [
        {
          DeadlineDate: new Date("2021-03-01T00:00:00"),
          DeadlineTake: 0,
          FinalDuration: 0,
          LearningFile: "SAT-Reading-Unit1.mp4",
          LearningName: "SAT-Reading-Unit1",
          LearningUrl: "https://stream.hkxf.org:1935/sat/_definst_/Done/SPW-1.mp4/playlist.m3u8?satendtime=0&satstarttime=0&sathash=asvbqCcuMt-YM7X66K6kBkiPphjAYexydkuAzoY5zJI=",
          LessonIndex: 95,
          LessonName: "SAT Reading ",
          LessonNo: 1025,
          Para: null,
          StayDuration: 335,
          StopDuration: 552,
          StudentNo: 10,
          TotalDuration: 552
          },
          {
          DeadlineDate: new Date("2021-03-08T00:00:00"),
          DeadlineTake: 1,
          FinalDuration: 0,
          LearningFile: "SAT-Math-Unit1.mp4",
          LearningName: "SAT-Math-Unit1",
          LearningUrl: "https://stream.hkxf.org:1935/sat/_definst_/Done/SPW-4.mp4/playlist.m3u8?satendtime=0&satstarttime=0&sathash=XDZ6zii2DbBY8od6IjUVCjTefzlFyYAts6x9ron_9X8=",
          LessonIndex: 120,
          LessonName: "SAT Math ",
          LessonNo: 1027,
          Para: null,
          StayDuration: 0,
          StopDuration: 0,
          StudentNo: 10,
          TotalDuration: 100
          }
      ]
    })
  }

}
