import { Component, OnInit, Inject, ViewChild, AfterViewInit } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import { NgForm, FormControl, NgModel } from '@angular/forms';
import * as moment from 'moment';

/**
 * @param title [string?] -  標頭
 * @param text1 [string?] -  段落一
 * @param text2 [string?] -  段落二
 * @param text3 [string?] -  段落三
 * @param leftBtnText [string?] -  副按鈕名稱
 * @param rightBtnText [string?] -  主按鈕名稱
 */
export interface Confirm {
  title?: string;
  text1?: string;
  text2?: string;
  text3?: string;
  leftBtnText: string;
  rightBtnText: string;
}

@Component({
  selector: 'app-template-drive',
  templateUrl: './template-drive.component.html',
  styleUrls: ['./template-drive.component.css']
})
export class TemplateDriveComponent implements OnInit, AfterViewInit {
  @ViewChild('form', {static: false}) form: NgForm;

  a: string;
  b: string;
  c: string;
  d: string;
  e: string;
  f: string;
  g: string;
  h: string;

  // 跟隨的章節id 的公開日期
  minDate: moment.Moment = moment(new Date());

  get time() {
    return this.form.control.get('time') as FormControl;
  }

  constructor(
    public dialogRef: MatDialogRef<TemplateDriveComponent>,
    @Inject(MAT_DIALOG_DATA) public data: Confirm) {}

  ngOnInit() {
  }

  ngAfterViewInit() {

  }

  onNoClick(): void {
    this.dialogRef.close();
  }

  timeChange(time: {hour: number, minute: number}) {
    if (! this.time) {
      this.form.control.addControl('time', new FormControl(time));
    }
    this.time.setValue(time);
    // console.log('TemplateDriveComponent timeChange time', time);
    // console.log('TemplateDriveComponent timeChange hour', time.hour);
    // console.log('TemplateDriveComponent timeChange minute', time.minute);

  }

  onSubmit() {

  }

}
