import { LearningCalendarComponent } from './learning-calendar/learning-calendar.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AssignmentComponent } from './assignment/assignment.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { LearningComponent } from './learning/learning.component';
import { ReviewComponent } from './review/review.component';
import { StudentComponent } from './student.component';
import { CurriculumComponent } from './curriculum/curriculum.component';

const routes: Routes = [
  {
    path: '',
    component: StudentComponent,
    children: [
      { path: '', component: DashboardComponent },
      { path: 'dashboard/curriculum', component: CurriculumComponent },
      { path: 'dashboard', component: DashboardComponent },
      { path: 'diagnostic', loadChildren: () => import('./diagnostic/diagnostic.module').then(m => m.DiagnosticModule) },
      { path: 'learning', component: LearningComponent },
      { path: 'review', component: ReviewComponent },
      { path: 'assignment', component: AssignmentComponent },
      { path: 'reports', loadChildren: () => import('./reports/reports.module').then(m => m.ReportsModule) },
      { path: 'learning/calendar', component: LearningCalendarComponent },
      { path: '', redirectTo: 'dashboard' , pathMatch: 'full' },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class StudentRoutingModule { }
