import { UpdatePhotoComponent } from '../../share/dialog/update-photo/update-photo.component';
import { MatDialog } from '@angular/material/dialog';
import { Component, OnInit } from '@angular/core';
import { printConsole } from '../../core/services/common.service';

@Component({
  selector: 'app-information-tch',
  templateUrl: './information-tch.component.html',
  styleUrls: ['./information-tch.component.css']
})
export class InformationTchComponent implements OnInit {

  isEdit = false;

  announcement = `A teacher (also called a school teacher or, in some contexts, an educator) is a person who helps students
  to acquire knowledge, competence or virtue.

  Informally the role of teacher may be taken on by anyone (e.g. when showing a colleague how to perform a
  specific task). In some countries, teaching young people of school age may be carried out in an informal
  setting, such as within the family (homeschooling), rather than in a formal setting such as a school or
  college. Some other professions may involve a significant amount of teaching (e.g. youth worker, pastor).

  In most countries, formal teaching of students is usually carried out by paid professional teachers. This
  article focuses on those who are employed, as their main role, to teach others in a formal education
  context, such as at a school or other place of initial formal education or training.`;

  constructor(
    private dialog: MatDialog
  ) { }

  ngOnInit(): void {
  }

  updatePhoto() {
    // closeOnNavigation 導航的時候是否關閉 dialog
    const dialogRef = this.dialog.open(UpdatePhotoComponent, {
      width: '350px',
      data: '',
      closeOnNavigation: false,
      disableClose: true,
      maxHeight: '90vh'
    });

    dialogRef.afterClosed().subscribe(result => {
      printConsole('The dialog update photo was closed', result);
    });
  }

}
