import { AbstractControl } from '@angular/forms';

export function TrimRequiredValidatorr(control: AbstractControl): { [key: string]: any } | null {
  // console.log('TrimRequiredValidatorr control value', control.value);
  const value = control.value;
  if (! value) { return { appTrimRequiredValidator: { valid: false }}; }
  const trimText = String(value) ? String(value).trim() : '';
  // console.log('TrimRequiredValidatorr trimText', trimText);
  // console.log('TrimRequiredValidatorr trimText length', trimText.length);
  return (trimText.length === 0) ? { appTrimRequiredValidator: { valid: false } } : null;
}
