import { ForgetPdComponent } from './../share/dialog/forget-pd/forget-pd.component';
import { MatDialog } from '@angular/material/dialog';
import { Component, OnInit, Inject, OnDestroy } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { faClock, faLock, faUser } from '@fortawesome/free-solid-svg-icons';
import { printConsole } from '../core/services/common.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit, OnDestroy {
  faClock = faClock;
  faUser = faUser;
  faLock = faLock;

  pdType = 'text';
  isText = true;

  pd = '';

  constructor(
    private dialog: MatDialog,
    @Inject(DOCUMENT) private document,
  ) { }

  ngOnInit(): void {
    this.document.body.classList.add('bg-white');
  }

  ngOnDestroy(): void {
    this.document.body.classList.remove('bg-white');
  }

  changePdType() {
    this.isText = ! this.isText;
    this.pdType = this.isText ? 'text' : 'password';
  }

  openForgetPassword() {
    // closeOnNavigation 導航的時候是否關閉 dialog
    const dialogRef = this.dialog.open(ForgetPdComponent, {
      width: '340px',
      data: null,
      closeOnNavigation: false,
      disableClose: true,
    });

    dialogRef.afterClosed().subscribe(result => {
      printConsole('The dialog forget password was closed', result);
    });
  }

}
