import { MediaMatcher } from '@angular/cdk/layout';
import { AfterViewInit, ChangeDetectorRef, Component, OnDestroy, OnInit } from '@angular/core';
import { ActivationEnd, Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { filter } from 'rxjs/operators';

@Component({
  selector: 'app-student',
  templateUrl: './student.component.html',
  styleUrls: ['./student.component.css']
})
export class StudentComponent implements OnInit, AfterViewInit, OnDestroy {

  mobileQuery: MediaQueryList;
  fillerNav = [
    { name: 'Dashboard', router: 'dashboard' , iconName: 'dashboard'},
    { name: 'Diagnostic Test', router: 'diagnostic' , iconName: 'flaky'},
    { name: 'Learning Plan', router: 'learning' , iconName: 'import_contacts'},
    { name: 'Test Review', router: 'review' , iconName: 'preview'},
    { name: 'Assignment', router: 'assignment' , iconName: 'assignment'},
    { name: 'Reports', router: 'reports' , iconName: 'analytics' }
  ];

  nowPage = { name: '表單', router: 'table' };

  private _mobileQueryListener: () => void;

  isDetailBar = false;

  detailList = [{
    compareName: 'learning/calendar',
    title: 'LEARNING SCHEDULE',
    url: '/student/learning'
  },
  {
    compareName: 'dashboard/curriculum',
    title: 'CURRICULUM',
    url: '/student/dashboard'
  },
  {
    compareName: 'reports/score',
    title: 'SCORE REPORT',
    url: '/student/reports/practice'
  },
  {
    compareName: 'reports/analysis',
    title: 'ABILITY ANALYSIS',
    url: '/student/reports/practice'
  }];

  targetDetail = null;

  routerSubscription: Subscription;

  constructor(
    changeDetectorRef: ChangeDetectorRef,
    media: MediaMatcher,
    private router: Router) {
    this.mobileQuery = media.matchMedia('(max-width: 500px)'); // 有誤差值 側選單 RWD 動態計算
    this._mobileQueryListener = () => changeDetectorRef.detectChanges();
    this.mobileQuery.addListener(this._mobileQueryListener);
  }

  ngOnInit() {
    this.titleEvent();
    this.checkIsDeatailBar();
  }

  ngAfterViewInit() {
  }

  ngOnDestroy(): void {
    this.routerSubscription.unsubscribe();
    this.mobileQuery.removeListener(this._mobileQueryListener);
  }

  /**
   * 綁定修改 title 事件
   */
  titleEvent() {
    // 初次進入
    this.changeTitle();

    this.routerSubscription = this.router.events.pipe(filter((event) => event instanceof ActivationEnd))
      .subscribe((event: ActivationEnd) => {
        this.changeTitle();
        this.checkIsDeatailBar();
      });
  }

  private changeTitle() {
    const path = this.router.url;
    const key = path.split('/')[2];
    console.log('management path', path);
    console.log('management key', key);

    switch (key) {
      case 'dashboard':
        this.nowPage = { name: 'Dashboard', router: 'dashboard' };
        break;
      case 'diagnostic':
        this.nowPage = { name: 'Diagnostic Test', router: 'diagnostic' };
        break;
      case 'learning':
        this.nowPage = { name: 'Learning Plan', router: 'learning' };
        break;
      case 'review':
        this.nowPage = { name: 'Test Review', router: 'review' };
        break;
      case 'assignment':
        this.nowPage = { name: 'Assignment', router: 'assignment' };
        break;
      case 'reports':
        this.nowPage = { name: 'Reports', router: 'reports' };
        break;
      default:
        this.nowPage = { name: 'Dashboard', router: 'dashboard' };
    }
  }

  private checkIsDeatailBar() {
    const filterDetail = this.detailList.filter( d => this.router.url.includes(d.compareName));
    this.isDetailBar = filterDetail.length === 1 ? true : false;
    this.targetDetail = this.isDetailBar ? filterDetail[0] : '';

  }

}
