import { MediaMatcher } from '@angular/cdk/layout';
import { AfterViewInit, ChangeDetectorRef, Component, OnDestroy, OnInit } from '@angular/core';
import { ActivationEnd, Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { filter } from 'rxjs/operators';
import { AuthService } from '../core/authentication/auth.service';

@Component({
  selector: 'app-teacher',
  templateUrl: './teacher.component.html',
  styleUrls: ['./teacher.component.css']
})
export class TeacherComponent implements OnInit, AfterViewInit, OnDestroy {

  mobileQuery: MediaQueryList;
  fillerNav = [
    { name: 'dashboard', router: 'dashboard' },
    { name: 'information', router: 'information' },
    { name: 'essay', router: 'essay' },
    { name: 'paysheet', router: 'paysheet' },
    { name: 'feedback', router: 'feedback' },
  ];

  nowPage = { name: '表單', router: 'table' };

  private _mobileQueryListener: () => void;

  routerSubscription: Subscription;

  constructor(
    changeDetectorRef: ChangeDetectorRef,
    media: MediaMatcher,
    private authService: AuthService,
    private router: Router) {
    this.mobileQuery = media.matchMedia('(max-width: 700px)'); // 有誤差值 側選單 RWD 動態計算
    this._mobileQueryListener = () => changeDetectorRef.detectChanges();
    this.mobileQuery.addListener(this._mobileQueryListener);
  }

  ngOnInit() {
    this.titleEvent();
  }

  ngAfterViewInit() {
  }

  ngOnDestroy(): void {
    this.routerSubscription.unsubscribe();
    this.mobileQuery.removeListener(this._mobileQueryListener);
  }

  /**
   * 綁定修改 title 事件
   */
  titleEvent() {
    // 初次進入
    this.changeTitle();

    this.routerSubscription = this.router.events.pipe(filter((event) => event instanceof ActivationEnd))
      .subscribe((event: ActivationEnd) => {
        this.changeTitle();
      });
  }

  private changeTitle() {
    const path = this.router.url;
    const key = path.split('/')[2];
    console.log('management path', path);
    console.log('management key', key);

    switch (key) {
      case 'dashboard':
        this.nowPage = { name: 'dashboard', router: 'dashboard' };
        break;
      case 'information':
        this.nowPage = { name: 'information', router: 'information' };
        break;
      case 'essay':
        this.nowPage = { name: 'essay', router: 'essay' };
        break;
      case 'paysheet':
        this.nowPage = { name: 'paysheet', router: 'paysheet' };
        break;
      case 'feedback':
        this.nowPage = { name: 'feedback', router: 'feedback' };
        break;
      case 'contact':
        this.nowPage = { name: 'contact', router: 'contact' };
        break;
      default:
        this.nowPage = { name: 'dashboard', router: 'dashboard' };
    }
  }

}
