import { Component, OnInit, ChangeDetectorRef, OnDestroy, AfterViewInit } from '@angular/core';
import { MediaMatcher } from '@angular/cdk/layout';
import { AuthService } from '../core/authentication/auth.service';
import { Router, ActivationEnd, ActivationStart } from '@angular/router';
import { Subscription } from 'rxjs';
import { filter } from 'rxjs/operators';

@Component({
  selector: 'app-tp',
  templateUrl: './tp.component.html',
  styleUrls: ['./tp.component.css']
})
export class TpComponent implements OnInit, AfterViewInit, OnDestroy {

  mobileQuery: MediaQueryList;
  fillerNav = [
    {name: '表單', router: 'table'},
    {name: '表單API', router: 'table-api'},
    {name: '圖表', router: 'chart'},
    {name: '管理', router: 'admin'}
  ];

  nowPage = {name: '表單', router: 'table'};

  private _mobileQueryListener: () => void;

  routerSubscription: Subscription;

  constructor(
    changeDetectorRef: ChangeDetectorRef,
    media: MediaMatcher,
    private authService: AuthService,
    private router: Router) {
    this.mobileQuery = media.matchMedia('(max-width: 700px)'); // 有誤差值 側選單 RWD 動態計算
    this._mobileQueryListener = () => changeDetectorRef.detectChanges();
    this.mobileQuery.addListener(this._mobileQueryListener);
  }

  ngOnInit() {
    this.titleEvent();
  }

  ngAfterViewInit() {
  }

  ngOnDestroy(): void {
    this.routerSubscription.unsubscribe();
    this.mobileQuery.removeListener(this._mobileQueryListener);
  }

  /**
   * 綁定修改 title 事件
   */
  titleEvent() {
    // 初次進入
    this.changeTitle();

    this.routerSubscription = this.router.events.pipe(filter((event) => event instanceof ActivationEnd))
      .subscribe((event: ActivationEnd) => {
        this.changeTitle();
    });
  }

  private changeTitle() {
    const path = this.router.url;
    const key = path.split('/')[2];
    console.log('management path', path);
    console.log('management key', key);

    switch (key) {
      case 'table':
        this.nowPage =  {name: '表單', router: 'table'};
        break;
      case 'table-api':
        this.nowPage = {name: '表單API', router: 'table-api'};
        break;
      case 'chart':
        this.nowPage =  {name: '圖表', router: 'chart'};
        break;
      case 'admin':
        this.nowPage =  {name: '管理', router: 'admin'};
        break;
      default:
        this.nowPage =  {name: '表單', router: 'table'};
    }
  }
}
