import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { filter, distinctUntilChanged } from 'rxjs/operators';
import { StorageService } from './storage.service';
import { UserInfo } from '../models/user-info';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  // 管理使用者資料是否符合登入的資格

  // 使用者資料 (應用程式)
  private userInfoSubject = new BehaviorSubject<UserInfo>(null);
  public userInfo = this.userInfoSubject.asObservable().pipe(
    filter(user => user !== null && user !== undefined),
    distinctUntilChanged());

  /**
   * 是否有登入 ( 以應用程式內是否有使用者資料為判斷 )
   * @readonly
   */
  get isSign() {
    return this.userInfoSubject.getValue() ? true : false;
  }

  /**
   * 權限驗證
   */
  get level() {
    return 1;
  }

  // get token() {
  //   return this.userInfoSubject.getValue() ? this.userInfoSubject.getValue().token : null;;
  // }

  constructor(private storageService: StorageService) { }

  /**
   * 儲存/更新使用者資料 (應用程式 和 本地端儲存庫)
   * @param userInfo []
   */
  saveUserInfo(userInfo) {
    this.userInfoSubject.next(userInfo);
    this.storageService.saveUserInfo(userInfo);
  }

  /**
   * 清除使用者資料 (應用程式 和 本地端儲存庫)
   */
  clearUserInfo() {
    this.userInfoSubject.next(null);
    this.storageService.clearAll();
  }


}
