  ngOnInit() {
    this.initDataSource();
  }






  selectX(select: MatSelect) {
    this.xSelected = select.value;
  }

  removeItems() {
    this.openDeletes(this.selection.selected);
    console.log('DialogTableComponent selection.selected', this.selection.selected);
  }

  doEdit() {

  }

  doDelete() {
    this.openDelete();
  }






  private openDelete() {
    const confitmContent: Confirm = {
      title: '是否確定要刪除？',
      leftBtnText: '取消',
      rightBtnText: '刪除'
    };

    const dialogRef = this.dialog.open(ConfirmComponent, {
      width: '250px',
      data: confitmContent,
      closeOnNavigation: false,
      disableClose: true
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog confirm was closed', result);
    });
  }

  private openDeletes(list: PeriodicElement[]) {
    const filterList = list.map( item => item.name);
    const filterText = filterList.join(',');
    const confitmContent: Confirm = {
      title: `是否確定要刪除${filterText}？`,
      leftBtnText: '取消',
      rightBtnText: '刪除'
    };

    const dialogRef = this.dialog.open(ConfirmComponent, {
      width: '250px',
      data: confitmContent,
      closeOnNavigation: false,
      disableClose: true
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog confirm was closed', result);
    });
  }








  // ----- 表單
  initDataSource() {
    this.isLoading = true;
    this.dataSource = new MatTableDataSource<PeriodicElement>(ELEMENT_DATA);
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
    this.isLoading = false;
  }

  applyFilter(event: Event) {
    // console.log('dataSource', this.dataSource);
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    this.isAllSelected() ?
      this.selection.clear() :
      // 直接全選無視過濾
      this.dataSource.data.forEach(row => this.selection.select(row));
    // 只選擇過濾資料
    // this.dataSource.filteredData.forEach(row => this.selection.select(row));
  }

  /** The label for the checkbox on the passed row */
  checkboxLabel(row?: PeriodicElement): string {
    if (!row) {
      return `${this.isAllSelected() ? 'select' : 'deselect'} all`;
    }
    return `${this.selection.isSelected(row) ? 'deselect' : 'select'} row ${row.position + 1}`;
  }

  /**
   * 自訂表單過濾規則
   */
  private createFilterPredicate() {
    // data 是原始資料， filter 是字串(原本是物件)
    this.dataSource.filterPredicate = (data: any, filter: string): boolean => {
      // zoneMap: 行政區; text: 行政區以外其他
      const filterObject: { zoneMap: string, text: string } = JSON.parse(filter);
      return ((data.accCode ? data.accCode.toLowerCase() : '').indexOf(filterObject.text) !== -1 ||
        (data.address ? data.address : '').indexOf(filterObject.text) !== -1 ||
        (data.name ? data.name : '').indexOf(filterObject.text) !== -1 ) &&
        (data.zoneName === filterObject.zoneMap || filterObject.zoneMap === null);
    };
  }

