import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { TpComponent } from './tp.component';
import { AuthGuard } from '../core/guard/auth.guard';
import { AaaComponent } from './aaa/aaa.component';
import { ChartComponent } from './chart/chart.component';
import { TableComponent } from './table/table.component';
import { TableApiComponent } from './table-api/table-api.component';

const routes: Routes = [
  {
    path: '',
    component: TpComponent,
    children: [
      {
        path: '',
        // canActivateChild: [AuthGuard],
        children: [
          { path: 'table', component: TableComponent },
          { path: 'table-api', component: TableApiComponent },
          { path: 'chart', component: ChartComponent },
          { path: 'admin', component: AaaComponent, canActivate: [AuthGuard] },
          { path: '', redirectTo: 'table' , pathMatch: 'full' },
        ]
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TpRoutingModule { }
