  ngAfterViewInit() {
    // 搜尋關鍵字
    // this.filterTextNg.valueChanges.subscribe(value => {
    //   console.log('filter text', value);
    // });

    // 下拉選單過濾
    // this.xSelectedNg.valueChanges.subscribe(value => {
    //   console.log('xSelect value', value);
    // });

    // If the user changes the sort order, reset back to the first page.
    this.sort.sortChange.subscribe(() => this.paginator.pageIndex = 0);

    merge(this.sort.sortChange, this.paginator.page, this.filterTextNg.valueChanges.pipe(debounceTime(500)), this.xSelectedNg.valueChanges)
      .pipe(
        startWith([]),
        switchMap(() => {
          this.isLoading = true;
          // active 目前激活的標頭，direction 升冪或降冪，分頁目前頁碼
          // console.log('sort active', this.sort.active);
          // console.log('sort direction', this.sort.direction);
          // console.log('paginator page index', this.paginator.pageIndex);
          return this.getSortData(this.sort.active, this.sort.direction, this.paginator.pageIndex, this.filterText, this.xSelected);
        }),
        map(data => {
          // Flip flag to show that loading has finished.
          this.isLoading = false;
          return data.items;
        }),
        catchError(() => {
          this.isLoading = false;
          return of([]);
        })
      ).subscribe(data => {
        this.dataSource = new MatTableDataSource<PeriodicElement>(ELEMENT_DATA);
        // this.dataSource = data as any;
      });
  }

