import { DialogNotificationComponent } from './../../dialog/dialog-notification/dialog-notification.component';
import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { faClock } from '@fortawesome/free-solid-svg-icons';
import { printConsole } from '../../../core/services/common.service';

@Component({
  selector: 'app-notification',
  templateUrl: './notification.component.html',
  styleUrls: ['./notification.component.css']
})
export class NotificationComponent implements OnInit {
  hasContent = true;
  faClock = faClock;
  constructor(private dialog: MatDialog) { }

  ngOnInit(): void {
  }

  playNotification() {
    // closeOnNavigation 導航的時候是否關閉 dialog
    const dialogRef = this.dialog.open(DialogNotificationComponent, {
      width: '65vw',
      data: '',
      closeOnNavigation: false,
      disableClose: true,
      maxHeight: '90vh',
      maxWidth: '100%'
    });

    dialogRef.afterClosed().subscribe(result => {
      printConsole('The dialog video player was closed', result);
    });
  }

}
