import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormGroup, FormControl, Validators, FormArray } from '@angular/forms';
import { TrimRequiredValidatorr } from '../../validators/trim-required-validatorr';
import { TrimMaxlengthValidator } from '../../validators/trim-maxlength-validator';

/**
 * @param title [string?] -  標頭
 * @param text1 [string?] -  段落一
 * @param text2 [string?] -  段落二
 * @param text3 [string?] -  段落三
 * @param leftBtnText [string?] -  副按鈕名稱
 * @param rightBtnText [string?] -  主按鈕名稱
 */
export interface Confirm {
  title?: string;
  text1?: string;
  text2?: string;
  text3?: string;
  leftBtnText: string;
  rightBtnText: string;
}

@Component({
  selector: 'app-reactive-form',
  templateUrl: './reactive-form.component.html',
  styleUrls: ['./reactive-form.component.css']
})
export class ReactiveFormComponent implements OnInit {

  form = new FormGroup({
    a: new FormControl('', [Validators.required]),
    b: new FormControl('', [TrimRequiredValidatorr]),
    c: new FormControl('', [TrimMaxlengthValidator(5)]),
    d: new FormControl(''),
    e: new FormControl('', [Validators.pattern('^\\w+((-\\w+)|(\\.\\w+))*\\@[A-Za-z0-9]+((\\.|-)[A-Za-z0-9]+)*\\.[A-Za-z]+$')]),
    f: new FormControl(''),
    g: new FormControl(''),
    h: new FormControl('', [Validators.pattern('^[0-9]{8,11}$')]),
    defaultArray: new FormArray([
      new FormGroup({
        j: new FormControl(''),
        k: new FormControl(''),
      })
    ]),
    nestArray: new FormArray([
      new FormGroup({
        l: new FormArray([
          new FormGroup({
            n: new FormControl('')
          })
        ]),
        o: new FormControl(''),
      })
    ])
  });

  get a() {
    return this.form.get('a') as FormControl;
  }

  get b() {
    return this.form.get('b') as FormControl;
  }

  get c() {
    return this.form.get('c') as FormControl;
  }

  get d() {
    return this.form.get('d') as FormControl;
  }

  get e() {
    return this.form.get('e') as FormControl;
  }

  get f() {
    return this.form.get('f') as FormControl;
  }

  get g() {
    return this.form.get('g') as FormControl;
  }

  get h() {
    return this.form.get('h') as FormControl;
  }

  get defaultList() {
    return this.form.get('defaultArray') as FormArray;
  }

  get nestList() {
    return this.form.get('nestArray') as FormArray;
  }

  lList(fg: FormGroup) {
    return fg.get('l') as FormArray;
  }

  get time() {
    return this.form.get('time') as FormControl;
  }

  constructor(
    public dialogRef: MatDialogRef<ReactiveFormComponent>,
    @Inject(MAT_DIALOG_DATA) public data: Confirm) { }

  ngOnInit() {
    console.log('form', this.e);
  }

  onNoClick(): void {
    this.dialogRef.close();
  }

  timeChange(time: { hour: number, minute: number }) {
    if (!this.time) {
      this.form.addControl('time', new FormControl(time));
    }
    this.time.setValue(time);
    // console.log('TemplateDriveComponent timeChange time', time);
    // console.log('TemplateDriveComponent timeChange hour', time.hour);
    // console.log('TemplateDriveComponent timeChange minute', time.minute);

  }

  addItem() {
    this.defaultList.push(new FormGroup({
      j: new FormControl(''),
      k: new FormControl(''),
    }));
  }

  addNest() {
    this.nestList.push(new FormGroup({
      l: new FormArray([
        new FormGroup({
          n: new FormControl('')
        })
      ]),
      o: new FormControl(''),
    }));
  }

  addNestInside(index: number) {
    const targetNest = this.nestList.at(index).get('l') as FormArray;
    targetNest.push(
      new FormGroup({
        n: new FormControl('')
      })
    );
  }

  onSubmit() {

  }
}
