import { Observable } from 'rxjs';
import { HttpService } from './../../../core/services/http.service';
import { Component, OnInit } from '@angular/core';
import { faArrowRight } from '@fortawesome/free-solid-svg-icons';
import { DateService } from '../../../core/services/date.service';
import { Response } from '../../../core/models/response';
import { ScheduleTwoDaysType, ScheduleTwoDaysModel } from './schedule-two-days.model';
import { tap } from 'rxjs/operators';

@Component({
  selector: 'app-schedule-two-days',
  templateUrl: './schedule-two-days.component.html',
  styleUrls: ['./schedule-two-days.component.css']
})
export class ScheduleTwoDaysComponent implements OnInit {
  faArrowRight = faArrowRight;
  firstDay = new Date();
  nextDay = new Date();
  oddDay = new Date();
  oddList: ScheduleTwoDaysType[] = [];
  evenDay = new Date();
  evenList: ScheduleTwoDaysType[] = [];

  constructor(private httpService: HttpService, private dateService: DateService) { }

  ngOnInit(): void {
    this.beEmptyScheduleTwoDays();

    const stringFirstDay = this.dateService.getDate(this.firstDay);

    this.nextDay = this.dateService.countDay(this.firstDay, 6);

    const stringNextDay = this.dateService.getDate(this.nextDay);

    console.log('ngOnInit first day', this.firstDay);
    console.log('ngOnInit next day', this.nextDay);

    // this.getScheduleTwoDays(stringFirstDay, stringNextDay);
    this.beMockScheduleTwoDays();
  }

  next() {
    this.countDaygetScheduleTwoDays(1);
  }

  previous() {
    this.countDaygetScheduleTwoDays(-1);
  }

  // 計算往前或後移日期並取得兩日行程
  countDaygetScheduleTwoDays(count: number) {
    console.log('first day', this.firstDay);
    console.log('next day', this.nextDay);
    this.firstDay = this.dateService.countDay(this.firstDay, count);
    this.nextDay = this.dateService.countDay(this.nextDay, count);
    const stringFirstDay = this.dateService.getDate(this.firstDay);
    const stringNextDay = this.dateService.getDate(this.nextDay);
    this.getScheduleTwoDays(stringFirstDay, stringNextDay);
  }

  getScheduleTwoDays(startDate, endDate) {
    this.httpService.post('https://selfcheck.hkxf.org/SATAPI/api/SAT/MyCurriculumList', {
      AuthTag: 'student',
      AuthToken: '123',
      StartDate: startDate,
      EndDate: endDate
    }).subscribe((res: Response<ScheduleTwoDaysType[]>) => {
      if (res.ApiStatus === true) {
        this.beFullScheduleTwoDays(res.ResultData);
        this.beMockScheduleTwoDays();
      } else {
        this.beEmptyScheduleTwoDays();
      }
    });
  }

  beFullScheduleTwoDays(data: ScheduleTwoDaysType[]) {
    this.oddList = [];
    this.evenList = [];

    data.forEach((r, index) => {
      if (index === 0) {
        this.oddDay = new Date(r.Date);
      }
      if (index === 1) {
        this.evenDay = new Date(r.Date);
      }
      if (index % 2 === 0) {
        this.oddList.push(r);
      } else {
        this.evenList.push(r);
      }
    });
  }

  beEmptyScheduleTwoDays() {
    this.oddList = [];
    this.evenList = [];

    for (let i = 1; i <= 7 ; i++) {
      this.oddList.push(new ScheduleTwoDaysModel());
      this.evenList.push(new ScheduleTwoDaysModel());
    }
  }

  beMockScheduleTwoDays() {
    this.oddList = [];
    this.evenList = [];

    this.oddDay = new Date('2021-03-23');
    this.evenDay = new Date('2021-03-24');

    this.oddList.push(new ScheduleTwoDaysModel('Vocabulary Training', 769, '2021-03-23'));
    this.evenList.push(new ScheduleTwoDaysModel('Vocabulary Training', 769, '2021-03-24'));

    this.oddList.push(new ScheduleTwoDaysModel('Reading-2', 769, '2021-03-23'));
    this.evenList.push(new ScheduleTwoDaysModel('Math-1', 769, '2021-03-24'));

    this.oddList.push(new ScheduleTwoDaysModel('Lunch Break', 769, '2021-03-23'));
    this.evenList.push(new ScheduleTwoDaysModel('Lunch Break', 769, '2021-03-24'));

    this.oddList.push(new ScheduleTwoDaysModel('Grammar-2', 769, '2021-03-23'));
    this.evenList.push(new ScheduleTwoDaysModel('Grammar-3', 769, '2021-03-24'));

    this.oddList.push(new ScheduleTwoDaysModel('Vocabulary Quiz', 769, '2021-03-23'));
    this.evenList.push(new ScheduleTwoDaysModel('Vocabulary Quiz', 769, '2021-03-24'));

    this.oddList.push(new ScheduleTwoDaysModel('Study Hall', 769, '2021-03-23'));
    this.evenList.push(new ScheduleTwoDaysModel('Study Hall', 769, '2021-03-24'));

  }
}
