import { ContactTchComponent } from './contact-tch/contact-tch.component';
import { FeedbackTchComponent } from './feedback-tch/feedback-tch.component';
import { PaysheetTchComponent } from './paysheet-tch/paysheet-tch.component';
import { EssayTchComponent } from './essay-tch/essay-tch.component';
import { InformationTchComponent } from './information-tch/information-tch.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DashboardTchComponent } from './dashboard-tch/dashboard-tch.component';
import { TeacherComponent } from './teacher.component';

const routes: Routes = [
  {
    path: '',
    component: TeacherComponent,
    children: [
      {
        path: '',
        // canActivateChild: [AuthGuard],
        children: [
          // { path: '', component: DashboardTchComponent },
          { path: 'dashboard', component: DashboardTchComponent },
          { path: 'information', component: InformationTchComponent },
          { path: 'essay', component: EssayTchComponent },
          { path: 'paysheet', component: PaysheetTchComponent },
          { path: 'feedback', component: FeedbackTchComponent },
          { path: 'contact', component: ContactTchComponent },
          { path: '', redirectTo: 'dashboard' , pathMatch: 'full' },
        ]
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TeacherRoutingModule { }
