import { Injectable } from '@angular/core';
import { UserInfo } from '../models/user-info';

@Injectable({
  providedIn: 'root'
})
export class StorageService {

  private key = 'userInfo';
  private isSave = true; // 是否持續儲存資料，true 放 local，false 放 session

  constructor() { }

  /**
   * 儲存/更新 使用者資料
   * @param user [UserInfo]
   */
  saveUserInfo(user: UserInfo) {
    if (window.localStorage === undefined) {
      console.log('瀏覽器過於老舊，無法儲存');
    }

    const storageUser = this.storageUserInfo(user);

    // 轉換成字串
    const str = JSON.stringify(storageUser);
    const encodeStr = encodeURI(str);

    if (this.isSave) {
      this.setLocalUserInfo(encodeStr);
    } else {
      this.setSessionUserInfo(encodeStr);
    }
  }

  /**
   * 取得帳號訊息(回傳 null/object )
   */
  getUserInfo(): UserInfo {
    const data = this.isSave ? this.getLocalUserInfo() : this.getSessionUserInfo();

    if (data === undefined) {
      return null;
    }

    // 轉換成物件
    const decodeStr = decodeURI(data);
    return JSON.parse(decodeStr) as UserInfo;
  }

  /**
   * 將權限資料從 storage 清除
   */
  clearAll() {
    localStorage.removeItem(this.key);
    sessionStorage.removeItem(this.key);
  }

  private storageUserInfo(user: UserInfo) {
    const storageUser: UserInfo = user;
    return storageUser;
  }

  /**
   * 取得長期資料
   */
  private getLocalUserInfo(): string {
    return localStorage[this.key];
  }

  /**
   * 儲存長期資料
   * @param data [string]
   */
  private setLocalUserInfo(data: string) {
    localStorage[this.key] = data;
  }


  /**
   * 取得短期資料
   */
  private getSessionUserInfo(): string {
    return sessionStorage[this.key];
  }

  /**
   * 儲存短期資料
   * @param data [string]
   */
  private setSessionUserInfo(data: string) {
    sessionStorage[this.key] = data;
  }
}
