import { PracticeTestComponent } from './practice-test/practice-test.component';
import { DiagnosticTestComponent } from './diagnostic-test/diagnostic-test.component';
import { DiagnosticComponent } from './diagnostic.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const routes: Routes = [
  {
    path: '',
    component: DiagnosticComponent,
    children: [
      { path: 'diagnosticTest', component: DiagnosticTestComponent },
      { path: 'practiceTest', component: PracticeTestComponent },
      { path: '', redirectTo: 'diagnosticTest' , pathMatch: 'full' },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DiagnosticRoutingModule { }
