import { AbilityAnalysisComponent } from './ability-analysis/ability-analysis.component';
import { ScoreReportComponent } from './score-report/score-report.component';
import { ReportsQuizComponent } from './reports-quiz/reports-quiz.component';
import { ReportsPracticeComponent } from './reports-practice/reports-practice.component';
import { ReportsLearningComponent } from './reports-learning/reports-learning.component';
import { ReportsAssignmentComponent } from './reports-assignment/reports-assignment.component';
import { ReportsAttendanceComponent } from './reports-attendance/reports-attendance.component';
import { ReportsComponent } from './reports.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const routes: Routes = [
  {
    path: '',
    component: ReportsComponent,
    children: [
      // { path: '', component: ReportsAttendanceComponent },
      { path: 'attendance', component: ReportsAttendanceComponent },
      { path: 'assignment', component: ReportsAssignmentComponent },
      { path: 'learning', component: ReportsLearningComponent },
      { path: 'practice', component: ReportsPracticeComponent },
      { path: 'quiz', component: ReportsQuizComponent },
      { path: 'score', component: ScoreReportComponent },
      { path: 'analysis', component: AbilityAnalysisComponent },
      { path: '', redirectTo: 'attendance' , pathMatch: 'full' },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ReportsRoutingModule { }
