import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class DateService {

  constructor() { }

  /**
   * 轉為 Server 可使用的時間字串
   * @param date [Date || string] 日期
   * @param hour [number] 時 - 0~23
   * @param minute [number] 分 0~59
   * @tip date 沒有時分，會加轉時區變成 T08:00
   */
  getServerTime(date: Date, hour?: number, minute?: number) {
    let hourText: string;
    let minuteText: string;
    let time: string;

    date = new Date(date);
    const monthText = (date.getMonth() + 1) < 10 ? String('0' + (date.getMonth() + 1)) : String(date.getMonth() + 1);
    const dateText = date.getDate() < 10 ? String('0' + date.getDate()) : String(date.getDate());

    if (hour || minute) {
      hourText = hour < 10 ? String('0' + hour) : String(hour);
      minuteText = minute < 10 ? String('0' + minute) : String(minute);
      time = String(date.getFullYear() + '-') + String(monthText + '-') + String(dateText + 'T') +
      String(hourText + ':') + String(minuteText);
      return time;
    }

    hourText = date.getHours() < 10 ? String('0' + date.getHours()) : String(date.getHours());
    minuteText = date.getMinutes() < 10 ? String('0' + date.getMinutes()) : String(date.getMinutes());
    time = String(date.getFullYear() + '-') + String(monthText + '-') + String(dateText + 'T') +
                 String(hourText + ':') + String(minuteText);
    return time;
  }

  /**
   * 取得日期的星期幾 (中文)
   * @param date [Date || string] 日期
   */
  getDayText(date: Date | string): string {
    const WEEKTEXT = ['日', '一', '二', '三', '四', '五', '六'];
    const dateFormat = new Date(date);
    return WEEKTEXT[(dateFormat.getDay())];
  }

  /**
   * 取得西元年 XXXX/XX/XX
   */
  getDate(date: Date | string): string {
    const dateFormat = new Date(date);
    const month = dateFormat.getMonth() + 1;
    const monthText = month < 10 ? '0' + month.toString() : month;
    const dateText = dateFormat.getDate() < 10 ? '0' + dateFormat.getDate().toString() : dateFormat.getDate();
    return `${dateFormat.getFullYear()}/${monthText}/${dateText}`;
  }

  /**
   * 取得台灣 XXX/XX/XX (X)
   */
  getTaiwanFullDate(date: Date | string): string {
    const dateFormat = new Date(date);
    const month = dateFormat.getMonth() + 1;
    const monthText = month < 10 ? '0' + month.toString() : month;
    const dateText = dateFormat.getDate() < 10 ? '0' + dateFormat.getDate().toString() : dateFormat.getDate();
    return `${dateFormat.getFullYear() - 1911}/${monthText}/${dateText} (${this.getDayText(dateFormat)})`;
  }

  /**
   * 取得台灣 XXX/XX/XX
   */
  getTaiwanDate(date: Date | string): string {
    const dateFormat = new Date(date);
    const month = dateFormat.getMonth() + 1;
    const monthText = month < 10 ? '0' + month.toString() : month;
    const dateText = dateFormat.getDate() < 10 ? '0' + dateFormat.getDate().toString() : dateFormat.getDate();
    return `${dateFormat.getFullYear() - 1911}/${monthText}/${dateText}`;
  }

  /**
   * 取得 00 ~ 23 數字
   */
  getHourList(): string[] {
    const hourList = [];
    for (let i = 0; i < 24; i++) {
      hourList.push(String(this.getNumber10plus0(i)));
    }
    return hourList;
  }

  /**
   * 取得 00 ~ 59 字串陣列
   */
  getMinuteList(): string[] {
    const minuteList = [];
    for (let i = 0; i < 60; i++) {
      minuteList.push(String(this.getNumber10plus0(i)));
    }
    return minuteList;
  }

  // 1-9數字補0
  private getNumber10plus0(value: any): string {
    let result = '00';
    if (!isNaN(value)) {
        result = value < 10 ? '0' + value.toString() : value.toString();
    }
    return result;
  }

  /**
   * 計算(+-) count 天的日期
   * @param date
   * @param count
   */
  public countDay(date: Date, count: number): Date {
    const originDate = new Date(date);
    originDate.setDate(originDate.getDate() + count);
    return new Date(originDate);
  }
}
