import { VideoPlayerComponent } from './../../share/dialog/video-player/video-player.component';
import { SetVideoDateComponent } from './../../share/dialog/set-video-date/set-video-date.component';
import { MatDialog } from '@angular/material/dialog';
import { DateService } from './../../core/services/date.service';
import { printConsole } from '../../core/services/common.service';
import { HttpService } from './../../core/services/http.service';
import { Component, OnInit } from '@angular/core';
import { faCircle, faExclamationTriangle, faStar } from '@fortawesome/free-solid-svg-icons';
import { BreakpointObserver } from '@angular/cdk/layout';
import { of } from 'rxjs';
import { getVideoStatus, LearningScheduleType, ResponseLearningScheduleType } from '../../core/models/learning-schedule.model';

@Component({
  selector: 'app-learning',
  templateUrl: './learning.component.html',
  styleUrls: ['./learning.component.css']
})
export class LearningComponent implements OnInit {
  STATUS = {
    NOTYET: 'NOTYET',
    UNFINISH: 'UNFINISH',
    FINISH: 'FINISH'
  };

  baseDate = new Date('1901-01-01'); // 判別是否顯示'預設文字'或'deadline日期'

  selectedSubject = 0; // 0 代表全部
  isPortrait = false; // 是否是縱向畫面
  faStar = faStar;
  faCircle = faCircle;
  faExclamationTriangle = faExclamationTriangle;

  originVideoList: LearningScheduleType[] = []; // API取得原始資料，不能改變

  filterVideoList: LearningScheduleType[] = []; // 顯示層用

  subjectList = [];

  today = new Date();
  startDate = new Date('2021-02-21');
  endDate = new Date('2021-03-20');

  constructor(
    private dialog: MatDialog,
    private breakpointObserver: BreakpointObserver,
    private httpService: HttpService,
    private dateServive: DateService,
  ) { }

  ngOnInit(): void {
    this.breakpointObserver.observe('(orientation: portrait)').subscribe(result => {
      this.isPortrait = result.matches;
    });

    this.getMockList().subscribe( (res: any) => {
      if (res.ApiStatus === true) {

        this.subjectList = this.getSubjectList(res.ResultData);
        this.originVideoList = this.getVideoList(res.ResultData);

        this.getFilterVideoList();

        printConsole('filter video list', this.filterVideoList);

      } else {

      }
    });
  }

  // 科目 Select 改變事件
  changeSubject(value: number) {
    this.selectedSubject = value;
    this.getFilterVideoList();
  }

  // 取得顯示用的影片列表
  getFilterVideoList() {
    if (this.selectedSubject === 0) {
      this.filterVideoList = [].concat(this.originVideoList);
    } else {
      this.filterVideoList = [].concat(this.originVideoList.filter(v => v.LessonIndex === this.selectedSubject));
    }
  }

  getVideoList(videoList: LearningScheduleType[]): LearningScheduleType[] {
    return videoList.map(v => {
      v.status = getVideoStatus(new Date(v.DeadlineDate), new Date(), v);
      v.percent = v.StopDuration === 0 ? 0 : Math.floor(v.TotalDuration / v.StopDuration)*100;
      v.isEdit = true; // 暫時都 true
      v.isDefaultText = this.baseDate > new Date(v.DeadlineDate);
      return JSON.parse(JSON.stringify(v));
    });
  }

  getSubjectList(videoList: LearningScheduleType[]) {

    // 等待插入 LessonName
    const subjectList = videoList.map((v, index) => {
      return {
        index: v.LessonIndex,
        name: `${v.LessonName}`
      };
    });

    return subjectList.filter((subject, index, array) => {
      return array.indexOf(subject) === index;
    });
  }

  getMockList() {
    return of({
      ApiStatus: true,
      ResultData: [
        {
          DeadlineDate: new Date("2021-03-01T00:00:00"),
          DeadlineTake: 0,
          FinalDuration: 0,
          LearningFile: "SAT-Reading-Unit1.mp4",
          LearningName: "SAT-Reading-Unit1",
          LearningUrl: "https://stream.hkxf.org:1935/sat/_definst_/Done/SPW-1.mp4/playlist.m3u8?satendtime=0&satstarttime=0&sathash=asvbqCcuMt-YM7X66K6kBkiPphjAYexydkuAzoY5zJI=",
          LessonIndex: 95,
          LessonName: "SAT Reading ",
          LessonNo: 1025,
          Para: null,
          StayDuration: 335,
          StopDuration: 552,
          StudentNo: 10,
          TotalDuration: 552
          },
          {
          DeadlineDate: new Date("2021-03-08T00:00:00"),
          DeadlineTake: 1,
          FinalDuration: 0,
          LearningFile: "SAT-Math-Unit1.mp4",
          LearningName: "SAT-Math-Unit1",
          LearningUrl: "https://stream.hkxf.org:1935/sat/_definst_/Done/SPW-4.mp4/playlist.m3u8?satendtime=0&satstarttime=0&sathash=XDZ6zii2DbBY8od6IjUVCjTefzlFyYAts6x9ron_9X8=",
          LessonIndex: 120,
          LessonName: "SAT Math ",
          LessonNo: 1027,
          Para: null,
          StayDuration: 0,
          StopDuration: 0,
          StudentNo: 10,
          TotalDuration: 100
          }
      ]
    })
  }

  editVideoDate(video: LearningScheduleType) {
    // closeOnNavigation 導航的時候是否關閉 dialog
    const dialogRef = this.dialog.open(SetVideoDateComponent, {
      width: '350px',
      data: video,
      closeOnNavigation: false,
      disableClose: true,
      maxHeight: '90vh'
    });

    dialogRef.afterClosed().subscribe(result => {
      printConsole('The dialog set video date was closed', result);
    });
  }

  playVideo(url: string) {
    // closeOnNavigation 導航的時候是否關閉 dialog
    const dialogRef = this.dialog.open(VideoPlayerComponent, {
      width: this.isPortrait ? '100vw' : '50vw',
      data: {
        url
      },
      closeOnNavigation: false,
      disableClose: true,
      maxHeight: '90vh',
      maxWidth: '100%'
    });

    dialogRef.afterClosed().subscribe(result => {
      printConsole('The dialog video player was closed', result);
    });
  }

}
