import { Component, ElementRef, Inject, OnInit, ViewChild } from '@angular/core';
import { FormControl, NgForm } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import * as moment from 'moment';
import { faMeh, faFrown, faTired, faSmile, faLaughWink, faCircle,
  faCheckCircle, faThumbsUp } from '@fortawesome/free-solid-svg-icons';

@Component({
  selector: 'app-course-feedback-edit',
  templateUrl: './course-feedback-edit.component.html',
  styleUrls: ['./course-feedback-edit.component.css']
})
export class CourseFeedbackEditComponent implements OnInit {
  @ViewChild('form', {static: false}) form: NgForm;
  @ViewChild('file', { static: false }) file: ElementRef;

  d: any;
  note: string;

  faTired = faTired;
  faFrown = faFrown;
  faMeh = faMeh;
  faSmile = faSmile;
  faLaughWink = faLaughWink;
  faCircle = faCircle;
  faCheckCircle = faCheckCircle;
  faThumbsUp = faThumbsUp;

  // 跟隨的章節id 的公開日期
  minDate: moment.Moment = moment(new Date());

  get time() {
    return this.form.control.get('time') as FormControl;
  }

  constructor(public dialogRef: MatDialogRef<CourseFeedbackEditComponent>,
              @Inject(MAT_DIALOG_DATA) public data: any) { }

    ngOnInit() {
    }

    ngAfterViewInit() {

    }

    onNoClick(): void {
      this.dialogRef.close();
    }

    timeChange(time: {hour: number, minute: number}) {
      if (! this.time) {
        this.form.control.addControl('time', new FormControl(time));
      }
      this.time.setValue(time);
      // console.log('TemplateDriveComponent timeChange time', time);
      // console.log('TemplateDriveComponent timeChange hour', time.hour);
      // console.log('TemplateDriveComponent timeChange minute', time.minute);

    }

    onSubmit() {
    }

}
