import { Response } from './response';

export interface ResponseLearningScheduleType extends Response<LearningScheduleType[]> {
}

/**
 * @param StudentNo 學生編號
 * @param LessonIndex 科目相關連結編號
 * @param TotalDuration 影片總時間
 * @param StopDuration 目前觀看進度
 * @param StayDuration 目前停留影片時間
 * @param DeadlineDate 觀看影片截止日期;為學生自定截止日期
 * @param DeadlineTake 影片綁定"堂"數 (此為 共12堂的第3堂);堂數為0, 視為學生自定截止日期
 * @param Para 其它參數 (未來有需要再使用)
 * @param LearningName 影片名稱
 * @param LearningFile 檔案名稱
 * @param LearningUrl 播放網址
 * @param isSelected? 是否被選擇
 * @param isDefaultText? 是否顯示預設文字 'Select Date' 或 顯示 截止日期
 * @param isEdit? 是否可以編輯
 * @param status? 'NOTYET' 未開始 | 'UNFINISH' 失敗 | 'FINISH' 完成
 * @param percent? 百分比數字
 */
export interface LearningScheduleType {
  StudentNo: number;
  LessonIndex: number;
  TotalDuration: number;
  StopDuration: number;
  StayDuration: number;
  DeadlineDate: string;
  DeadlineTake: number;
  Para: string;
  LearningName: string;
  LearningFile: string;
  LessonName: string;
  LearningUrl: string;
  isSelected?: boolean;
  isDefaultText?: boolean;
  isEdit?: boolean;
  status?: 'NOTYET' | 'UNFINISH' | 'FINISH';
  percent?: number;
}

export interface DashboardLearningWeekType {
  date: Date;
  isSelected: boolean;
  notYet: number;
  unFinish: number;
  finish: number;
  videoList: LearningScheduleType[];
}


export class LearningScheduleModel {
  constructor(private videoList: LearningScheduleType[],
              private startDate: string,
              private endDate: string,
              private currectDate: string) {}

  getSettedVideoList() {
    const startDate = new Date(this.startDate);

    return this.videoList.filter( v => {
      return new Date(v.DeadlineDate) > startDate;
    });
  }

  getTotalStar() {
    const settedVideoList = [].concat(this.getSettedVideoList());

    return settedVideoList.filter( v => v.StopDuration !== 0 && v.StopDuration === v.TotalDuration).length;
  }

  /**
   * 取得 影片放置於單一日，將一個星期(一到五)放於一個 陣列，再將上列陣列放置陣列中(4個)
   * @returns DashboardLearningWeekType[][]
   */
  getVideoOfDayInWeekWithoutWeekend(): DashboardLearningWeekType[][] {
    const startDate = new Date(this.startDate);
    const endDate = new Date(this.endDate);
    const currectDate = new Date(this.currectDate);
    const baseDate = new Date('1901-01-01'); // 判別是否顯示'預設文字'或'deadline日期'

    // 過濾掉沒有設定截止日期的影片
    const filterNotSetStudyDateViedoList = this.videoList.filter( v => {
      return new Date(v.DeadlineDate) > startDate;
    });

    // 每周清單(一二三四五)
    const singleWeekList = [];
    let weekCount = 0;

    while (weekCount < 4) {

      let dayCount = 0;
      const singleWeek = [];
      while (dayCount < 7) {
        const dateForCount = countDay(startDate, 0 + (weekCount * 7) + dayCount);

        const videoListOfDay = filterNotSetStudyDateViedoList.filter( settedVideo => {
          return getTaiwanDate(settedVideo.DeadlineDate) === getTaiwanDate(dateForCount);
        });

        const isFuture = getTaiwanDate(dateForCount) >= getTaiwanDate(currectDate);
        const notYet = isFuture ? videoListOfDay.length : 0;
        const unFinish = isFuture ? 0 : videoListOfDay.filter( v => v.StopDuration !== v.TotalDuration).length;
        const finish = isFuture ? 0 : videoListOfDay.filter( v => v.StopDuration === v.TotalDuration).length;

        const videoList = videoListOfDay.map(v => {
          v.status = getVideoStatus(new Date(v.DeadlineDate), new Date(), v);
          v.percent = v.StopDuration === 0 ? 0 : Math.floor(v.TotalDuration / v.StopDuration);
          v.isEdit = true; // 暫時都 true
          v.isDefaultText = baseDate > new Date(v.DeadlineDate);
          return JSON.parse(JSON.stringify(v));
        });

        // 剔除禮拜日和禮拜六
        if (! (dateForCount.getDay() === 0 || dateForCount.getDay() === 6)) {
          singleWeek.push({
            date: dateForCount ,
            isSelected: false,
            notYet,
            unFinish,
            finish,
            videoList
          });
        }

        dayCount ++;
      }
      singleWeekList.push(singleWeek);
      weekCount ++;
    }

    return singleWeekList;

  }

  /**
   * 取得 影片放置於單一日，將一個星期(六到日)放於一個 陣列，再將上列陣列放置陣列中(4個)
   * @returns DashboardLearningWeekType[][]
   */
    getVideoOfDayInWeek(): DashboardLearningWeekType[][] {
      const startDate = new Date(this.startDate);
      const endDate = new Date(this.endDate);
      const currectDate = new Date(this.currectDate);
      const baseDate = new Date('1901-01-01'); // 判別是否顯示'預設文字'或'deadline日期'

      // 過濾掉沒有設定截止日期的影片
      const filterNotSetStudyDateViedoList = this.videoList.filter( v => {
        return new Date(v.DeadlineDate) > startDate;
      });

      // 每周清單(一二三四五)
      const singleWeekList = [];
      let weekCount = 0;

      while (weekCount < 4) {

        let dayCount = 0;
        const singleWeek = [];
        while (dayCount < 7) {
          const dateForCount = countDay(startDate, 0 + (weekCount * 7) + dayCount);

          const videoListOfDay = filterNotSetStudyDateViedoList.filter( settedVideo => {
            return getTaiwanDate(settedVideo.DeadlineDate) === getTaiwanDate(dateForCount);
          });

          const isFuture = getTaiwanDate(dateForCount) >= getTaiwanDate(currectDate);
          const notYet = isFuture ? videoListOfDay.length : 0;
          const unFinish = isFuture ? 0 : videoListOfDay.filter( v => v.StopDuration !== v.TotalDuration).length;
          const finish = isFuture ? 0 : videoListOfDay.filter( v => v.StopDuration === v.TotalDuration).length;

          const videoList = videoListOfDay.map(v => {
            v.status = getVideoStatus(new Date(v.DeadlineDate), new Date(), v);
            v.percent = v.StopDuration === 0 ? 0 : Math.floor(v.TotalDuration / v.StopDuration);
            v.isEdit = true; // 暫時都 true
            v.isDefaultText = baseDate > new Date(v.DeadlineDate);
            return JSON.parse(JSON.stringify(v));
          });

          singleWeek.push({
            date: dateForCount ,
            isSelected: false,
            notYet,
            unFinish,
            finish,
            videoList
          });

          dayCount ++;
        }
        singleWeekList.push(singleWeek);
        weekCount ++;
      }

      return singleWeekList;

    }

  /**
   * 取得 目前日期 位於 第幾 星期Index
   * @param date - Date - 目前日期
   * @returns weekIndex
   */
  getWeekIndexOfWeekList(date: Date) {
    const startDate = new Date(this.startDate);
    const endDate = new Date(this.endDate);
    const currectDate = new Date(this.currectDate);
    let weekIndex = 0;

    // 過濾掉沒有設定截止日期的影片
    const filterNotSetStudyDateViedoList = this.videoList.filter( v => {
      return new Date(v.DeadlineDate) > startDate;
    });

    let weekCount = 0;

    while (weekCount < 4) {
      let dayCount = 0;

      while (dayCount < 7) {
        const dateForCount = countDay(startDate, 0 + (weekCount * 7) + dayCount);
        if (getTaiwanDate(dateForCount) === getTaiwanDate(date)) {
          weekIndex = weekCount;
        }
        dayCount ++;
      }
      weekCount ++;
    }

    return weekIndex;
  }
}

/**
 * 計算(+-) count 天的日期
 */
function countDay(date: Date, count: number): Date {
  const originDate = new Date(date);
  originDate.setDate(originDate.getDate() + count);
  return new Date(originDate);
}

  /**
   * 取得台灣 XXX/XX/XX
   */
function getTaiwanDate(date: Date | string): string {
    const dateFormat = new Date(date);
    const month = dateFormat.getMonth() + 1;
    const monthText = month < 10 ? '0' + month.toString() : month;
    const dateText = dateFormat.getDate() < 10 ? '0' + dateFormat.getDate().toString() : dateFormat.getDate();
    return `${dateFormat.getFullYear() - 1911}/${monthText}/${dateText}`;
}

/**
 *
 * @param targetDate - Date - 檢測時間
 * @param currectDate - Date - 目前時間
 * @param video - LearningScheduleType - 影片物件
 * @returns 'NOTYET' | 'UNFINISH' | 'FINISH'
 */
export function getVideoStatus(targetDate: Date, currectDate: Date, video: LearningScheduleType): 'NOTYET' | 'UNFINISH' | 'FINISH'{

  const isFuture = getTaiwanDate(targetDate) >= getTaiwanDate(currectDate);

  if (isFuture) {
    return 'NOTYET';
  }

  if (video.StopDuration !== video.TotalDuration) {
    return 'UNFINISH';
  }

  if (video.StopDuration === video.TotalDuration) {
    return 'FINISH';
  }

}
