import { Directive, forwardRef, Input } from '@angular/core';
import { NG_VALIDATORS, FormControl, Validator, NgModel } from '@angular/forms';

@Directive({
  selector: '[appComparePd][ngModel],[formControlName][ngModel],[formControl][ngModel]',
  providers: [
    { provide: NG_VALIDATORS, useExisting: forwardRef(() => ComparePdDirective), multi: true }
  ]
})
export class ComparePdDirective {
  // 輸入參數值
  // tslint:disable-next-line: no-input-rename
  @Input('appComparePd') target: NgModel;

  validate(c: FormControl) {
    // console.log('ComparePdDirective comparePd', c.value);
    // console.log('ComparePdDirective targetPd', this.target.value);
    const trimText = (c.value) ? c.value.trim() : '';
    const targetText = this.target.value ? this.target.value.trim() : '';
    return trimText !== targetText ?  { appComparePd: { valid: false } } : null;
  }
}
