import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { SignInOutService } from '../core/authentication/sign-in-out.service';
import { MatDialog } from '@angular/material/dialog';
import { DateService } from '../core/services/date.service';
import { ConfirmComponent, Confirm } from '../core/dialog/confirm/confirm.component';
import { Alert, AlertComponent } from '../core/dialog/alert/alert.component';
import { DialogErrorComponent } from '../core/dialog/dialog-error/dialog-error.component';
import { TemplateDriveComponent } from '../core/dialog/template-drive/template-drive.component';
import { ReactiveFormComponent } from '../core/dialog/reactive-form/reactive-form.component';
import { DialogTableComponent } from '../core/dialog/dialog-table/dialog-table.component';
import { UploadingComponent } from '../core/dialog/uploading/uploading.component';
import { timer } from 'rxjs';
import { take } from 'rxjs/operators';

export interface UploadFile extends File {
  dataSource?: string;
  isImg?: boolean;
}

@Component({
  selector: 'app-xyzabc',
  templateUrl: './xyzabc.component.html',
  styleUrls: ['./xyzabc.component.css']
})
export class XyzabcComponent implements OnInit {
  @ViewChild('file', { static: false }) file: ElementRef;
  uploadFiles: UploadFile[] = [];

  title = 'core';
  foods = ['apple', 'banana', 'orange'];

  constructor(
    private signInOutService: SignInOutService,
    private dialog: MatDialog,
    private dateService: DateService
    ) { }

  ngOnInit(): void {
  }

  showFile() {
    this.uploadFiles = [];
    // console.log('ElementRef', this.file);
    const inputElement = this.file.nativeElement as HTMLInputElement;

    // ----- 多檔 ------
    const fileList = inputElement.files;
    Array.from(fileList).forEach(file => {
    // 先宣告行為
    const reader = new FileReader();
    reader.onload = (e: any) => {
      // console.log('e', e);
      // console.log('file', file);
      // data:xxxxxxxx (原始檔案解譯)
      const uploadFile: UploadFile = file;
      uploadFile.dataSource = e.target.result;
      uploadFile.isImg = file.type.includes('image');
      // console.log('isImg', uploadFile.isImg);
      this.uploadFiles.push(uploadFile);
    };
    // 再執行
    reader.readAsDataURL(file);
    });

    // 於 Angular 並不能直接使用
    // const bolbUrl = URL.createObjectURL(one);
    // console.log('bolb url', bolbUrl);

    // 瀏覽器位置
    // this.imgSrc = bolbUrl; // unSelf
  }

  removeFile(uploadFile: UploadFile) {
    const index: number = this.uploadFiles.indexOf(uploadFile);
    if (index !== -1) {
      this.uploadFiles.splice(index, 1);
    }
  }

  openConfirm() {
    const confitmContent: Confirm = {
      title: '是你在呼喚我嗎？',
      leftBtnText: '不是',
      rightBtnText: '是的'
    };

    // closeOnNavigation 導航的時候是否關閉 dialog
    const dialogRef = this.dialog.open(ConfirmComponent, {
      width: '250px',
      data: confitmContent,
      closeOnNavigation: false,
      disableClose: true
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog confirm was closed', result);
    });
  }

  openUploading() {
    // closeOnNavigation 導航的時候是否關閉 dialog
    const dialogRef = this.dialog.open(UploadingComponent, {
      width: '250px',
      closeOnNavigation: false,
      disableClose: true
    });

    timer(2000).pipe(take(1)).subscribe(() => {
      dialogRef.close();
    });

    // dialogRef.afterClosed().subscribe(result => {
    //   console.log('The dialog uploading was closed', result);
    // });
  }

  openAlert() {
    const confitmContent: Alert = {
      title: '歡迎來到 Core',
      btnText: '離開'
    };

    // closeOnNavigation 導航的時候是否關閉 dialog
    const dialogRef = this.dialog.open(AlertComponent, {
      width: '250px',
      data: confitmContent,
      closeOnNavigation: false,
      disableClose: true
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog alert was closed', result);
    });
  }

  openError() {
    // closeOnNavigation 導航的時候是否關閉 dialog
    const dialogRef = this.dialog.open(DialogErrorComponent, {
      width: '250px',
      closeOnNavigation: false,
      disableClose: true
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog alert was closed', result);
    });
  }

  openTemplateDrive() {
    // closeOnNavigation 導航的時候是否關閉 dialog
    const dialogRef = this.dialog.open(TemplateDriveComponent, {
      width: '350px',
      closeOnNavigation: false,
      disableClose: true
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog alert was closed', result);
    });
  }

  openReactiveForm() {
    // closeOnNavigation 導航的時候是否關閉 dialog
    const dialogRef = this.dialog.open(ReactiveFormComponent, {
      width: '600px',
      closeOnNavigation: false,
      disableClose: true
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The ReactiveForm alert was closed', result);
    });
  }

  openAngularMaterialTable() {
        // closeOnNavigation 導航的時候是否關閉 dialog
        const dialogRef = this.dialog.open(DialogTableComponent, {
          width: '600px',
          closeOnNavigation: false,
          disableClose: true
        });

        dialogRef.afterClosed().subscribe(result => {
          console.log('The dialog AngularMaterialTable was closed', result);
        });
  }
}
