import { Directive, forwardRef, Input } from '@angular/core';
import { NG_VALIDATORS, FormControl, Validator } from '@angular/forms';

// selector 表示選取器要符合才會觸發
@Directive({
  selector: '[appTrimLengthValidator][ngModel],[formControlName][ngModel],[formControl][ngModel]',
  providers: [
    { provide: NG_VALIDATORS, useExisting: forwardRef(() => TrimLengthValidatorDirective), multi: true }
  ]
})
export class TrimLengthValidatorDirective implements Validator {
  // 輸入參數值
  // tslint:disable-next-line: no-input-rename
  @Input('appTrimLengthValidator') trimMaxLength: string;

  validate(c: FormControl) {
    const trimText = (c.value) ? c.value.trim() : '';
    // 若設定最大字數
    if (this.trimMaxLength) {
      // 有，大於設定最大長度，則驗證失敗回傳失敗物件，反之驗證成功
      return trimText.length > this.trimMaxLength ? {
        appTrimLengthValidator: {
          valid: false
        }
      } : null;
    } else {
      // 無，則表示驗證都成功
      return null;
    }
  }

}
