import { AbstractControl, ValidatorFn } from '@angular/forms';

export function TrimMaxlengthValidator(maxLength: number): ValidatorFn {
  return (control: AbstractControl): {[key: string]: any} | null => {
    const trimText = (control.value) ? control.value.trim() : '';
    return trimText.length > maxLength ? {
      appTrimLengthValidator: {
        valid: false
      }
    } : null;
  };

}
